/*************************/
///// COMPONENT CODE //////
/*************************/

.social-buttons-container{
    position: relative;
    width:100%;
    height:100%;
    .share-button{
        display: inline-block;
        text-align: center;
        width:nth($button-widths,1); 
        height:nth($button-heights,1);
        line-height: nth($button-heights,1);
        i{
            vertical-align: middle;
            color:$icon-colour;
            font-size: nth($icon-font-sizes,1);
        }
        &.embed{
            display: none;
        }

    }


    @include MediumDevice{
        .share-button{

            &.embed{
                display: inline-block;
            }
        }
    }
}

/*************************/
/////// CUSTOM CODE /////// 
/*************************/

.social-buttons-container{
    .share-button{
        margin:0 10px 0px 0px; 

        &.inverted{
            background-color:$color-3; 
            i{
                color:$color-2;
            }
        }
    }
}