/*************************/
///// COMPONENT CODE //////
/*************************/
.embed-code-container{
    .embed-copy-btn{
        .btn-label{
            color:$color-1!important;
        }
        &:hover{
            .btn-label{
                color:$color-2;
            }
        }
    }
}

/*************************/
/////// CUSTOM CODE /////// 
/*************************/

.embed-code-container{
    .embed-options{
        .embed-option-item{
            @extend %no-user-select;
            width:50%; 
            float: left;
            position: relative;
            &:after{
                content:' '; 
                position: absolute;
                bottom:6px; 
                width:250px; 
                left:50%; 
                margin-left:-125px; 
                height:2px; 
                background-color:$color-5;
            }

            &:hover{
                cursor: pointer;
            }
        }
    }

    .routes-container{
        float: left; 
        width:100%;
        height:160px;
        text-align: center;
        overflow: hidden;
        margin:1ex 0px;
        .route-item-container{
            display: inline-block;
            width:160px;
            height:160px; 
            background-size: contain; 
            background-position: center center;
            background-repeat: no-repeat;
            @extend %box;

            &:hover{
                cursor: pointer;
                border:5px solid $color-2;
            }
            p{
                color:$color-2;
            }
        }

        &[data-current-route-id="s-0"]{
            .route-item-container[data-route-id="s-0"]{
                border:3px solid $color-2!important;
            }
        }
        &[data-current-route-id="s-1"]{
            .route-item-container[data-route-id="s-1"]{
                border:3px solid $color-2!important;
            }
        }
        &[data-current-route-id="s-2"]{
            .route-item-container[data-route-id="s-2"]{
                border:3px solid $color-2!important;
            }
        }

    }

    .embed-options-container{
        &[data-embed-option="whole"]{
            margin-bottom:50px;
            float:left; 
            width:100%;
            .embed-option-item.whole{
                &:after{
                    display: block;
                }
            }
            .embed-option-item.specific{
                color:$color-4;
                &:after{
                    display: none;
                }
            }
        }

        &[data-embed-option="specific"]{
            .embed-option-item.whole{
                color:$color-4;
                &:after{
                    display: none;
                }
            }
            .embed-option-item.specific{
                &:after{
                    display: block;
                }
            }
            
        }
    }

}

.embed-container{
    .embed-copy-container{
        text-align: center;
    }

    .embed-textarea{
        background-color:$color-2; 
        border:3px solid $color-5;
        height: 90px;
        margin-bottom:12px;
    }
    .embed-options{
        position: relative;
        float: left;
        width:100%;
        margin-bottom: 30px;
    }
}